const mail = (url, data, idResultMessage)=>{
    const resultText = document.getElementById(idResultMessage),
    text = document.querySelectorAll('textarea'),
    inputs = document.querySelectorAll('input');

    const clearInputs = () => {
        inputs.forEach(item => {
            item.value = '';
        });
        text.forEach(item => {
            item.value = '';
        });
        inputs.forEach(item => {
            if(item.getAttribute('type') === 'checkbox') {
                item.checked = false;
            }
        });
    };
    const message = {
        loading: '...',
        success: '!     ',
        failure: '-   ...',
        // spinner: 'icons/messages/spinner.gif',
        // ok: 'icons/messages/ok.png',
    }
    const postData = async (url, data) => {
        let res = await fetch(url, {
            method: 'POST',
            body: data
        });
        return await res.text();
    }
    let statusMessage = document.createElement('div');
    // statusMessage.classList.add('status');
    
    // const formData = new FormData(data);
    postData(url, data)
        .then(result =>{
            resultText.textContent = message.success;
        })
        .catch(() =>{
            resultText.textContent = message.failure;
        })
        .finally(()=>{
            clearInputs();
            setTimeout(()=>{
                resultText.remove();
            }, 2000);
        });
};
export default mail;