const sliders = (sliders, prevSelector, nextSelector) => {
    const slides = document.querySelectorAll(sliders),
        prevBtn = document.querySelector(prevSelector),
        nextBtn = document.querySelector(nextSelector);

    let slideIndex = 1,
        paused = false;

    function showSlides(n) {
        if (n > slides.length) {
            slideIndex = 1;
        }
        if (n < 0) {
            slideIndex = slides.length;
            // slides[slideIndex].classList.remove('slideshow__item--selected');
            // slides[slides.length].classList.add('slideshow__item--selected');
        }

        // slides.forEach(item => {
        //     // item.classList.add("--selected");
        //     item.style.display = "none";
        // })
        // slides[slideIndex - 1].classList.add = 'slideshow__item--selected';
    }

    function plusSlides(n) {
        showSlides(slideIndex += n);
    }
  

    try {
        prevBtn.addEventListener('click', () => {
            
            plusSlides(-1);
            slides[slideIndex].classList.remove('slideshow__item--selected');
            slides[slideIndex - 1].classList.add('slideshow__item--selected');
            
        });
        nextBtn.addEventListener('click', (e) => {
  
            slides[slideIndex-1].classList.remove('slideshow__item--selected');
            slides[slideIndex].classList.add('slideshow__item--selected');
            plusSlides(+1);
        });
    } catch (e) {}


};
export default sliders;